import { useState } from "react";
import { ReactComponent as SearchBtn } from "assets/search_btn.svg";
import styles from "css/FindPageTemplate.module.scss";
import Modal from "components/Modal";
import ProjectPreview from "components/ProjectPreview";
import { Link } from "react-router-dom";

let dummy_data = [
  {
    name: "피드백 보관 플랫폼 #posTree",
    projectID: 1,
    image: "1.png",
  },
  {
    name: "ArtRecipe: 예술작품 - 재료정보 공유 플랫폼",
    projectID: 2,
    image: "2.png",
  },
  {
    name: "PODO. 학습 목적의 영상 모음 웹사이트",
    projectID: 3,
    image: "3.png",
  },
  {
    name: "BookItOut: 고려대학교 학부생의 원활한 지식 정보 자료 이용을 위한 서비스",
    projectID: 4,
    image: "4.png",
  },
  {
    name: "목소리로 만나는 대학친구! 통화링",
    projectID: 5,
    image: "5.png",
  },
  {
    name: "모의 담금질 기법 시각화",
    projectID: 6,
    image: "6.png",
  },
  {
    name: "Borrow Go! 대학교 내 물건 대여 시스템",
    projectID: 7,
    image: "7.png",
  },
  {
    name: "고려대학교 스마트캠퍼스 커넥터 - 안전한 연구실 Wb-App",
    projectID: 8,
    image: "8.png",
  },
  {
    name: "오늘 뭐 먹지? 음식선호도 조사 후 인공지능이 추천",
    projectID: 9,
    image: "9.png",
  },
];

const FindPageTemplate = ({ FindPageType }) => {
  const [searchText, setSearchText] = useState("");
  const [isModalOn, setIsModalOn] = useState(false);
  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setSearchText(value);
  };
  const onSubmit = async (event) => {
    event.preventDefault();
  };
  const onFilterBtnClick = () => {
    setIsModalOn(true);
  };

  return (
    <>
      <div className={styles.search_filter_container}>
        <form onSubmit={onSubmit}>
          <div className={styles.search_box_container}>
            <input
              className={styles.search_textbox}
              type="text"
              placeholder=" search"
              onChange={onChange}
            />
            <button className={styles.search_btn}>
              <SearchBtn />
            </button>
          </div>
        </form>
        <div className={styles.filter_btn_container} onClick={onFilterBtnClick}>
          <img
            height="50px"
            width="50px"
            alt="small_puzzle_img"
            src={process.env.PUBLIC_URL + "/img/FindProject/small_puzzle.png"}
          ></img>
          <span>filter</span>
        </div>
      </div>
      <div className={styles.board_container}>
        <div className={styles.board_subcontainer}>
          <div className={styles.board_projects_for_you}>
            {FindPageType} for you!
          </div>
          <div className={styles.board_contents}>
            {dummy_data.map((item) => (
              <Link to={`/project/${item.projectID}`}>
                <ProjectPreview item={item} />
              </Link>
            ))}
          </div>
          <div className={styles.board_page_numbers}>1 2 3 4 5 {">"}</div>
        </div>
      </div>
      {isModalOn ? (
        <Modal modalTypeInput="Filter" setIsModalOn={setIsModalOn} />
      ) : null}
    </>
  );
};

export default FindPageTemplate;
